#skills{
    overflow: hidden;
    width: 100vw;
    /*max width left to right of website */
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    /*margin is 0 for top and bottom, and the left and right spacing is automatically set*/
    margin: 0 auto;
}

.skillTitle{
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}

.skillDescription{
    font-weight: 300;
    font-size: 1.10rem;
    max-width: 50rem;
    padding: 0 2rem;
}

.skillBars{
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;
}

.skillBar{
    display: flex;
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    background: rgb(50 , 50, 50);
}

.skillbarImg{
    object-fit: cover;
    height: 7rem;
    width: 7rem;
    margin-right: 2rem;
}

.skillBarText>p {
    font-size: 1.10rem;
    font-weight: 200;
}

@media screen and (max-width:720px) {
    .skillBarText>p{
        font-size: 2vw;
    }
}